import { Chrome } from 'vue-color';

const initial = {
  shape: 'Choose Shape',
  text: '',
  color: 'Choose Color'
};

const defaultColor = '#4885ed';

export default {
  name: 'MainForm.Component',
  data () {
    return {
      colors: defaultColor,
      currentShape: {
        shape: initial.shape,
        text: initial.text,
        color: initial.color
      },
      error: false,
      shapes: ['Circle', 'Rectangle', 'Square', 'Triangle'],
      view: 'default'
    }
  },
  methods: {
    validate () {
      this.error = false;

      // VALIDATE SHAPE
      if (!this.shapes.includes(this.currentShape.shape)) {
        console.error('No Shape Selected');
        this.error = true;
      }

      // VALIDATE COLOR
      if (this.currentShape.color === initial.color) {
        console.error('No Color Selected');
        this.error = true;
      }

      // VALIDATE TEXT
      if (this.currentShape.text === initial.text) {
        console.error('No Text Selected');
        this.error = true;
      }

      // DO NOT CONTINUE IF WE HAVE ERRORS
      if (this.error) return;

      // IF WE HAVE CLEARED VALIDATION,
      // SEND DATA TO PARENT COMPONENT
      this.$emit('createShape', this.currentShape);

      // RESET INITIAL STATE
      this.currentShape = {
        shape: initial.shape,
        text: initial.text,
        color: initial.color
      };
    },
    addShape (shape) {
      this.currentShape.shape = shape;

      // RETURN TO DEFAULT VIEW
      this.view = 'default';
    },
    addColor () {
      if (this.colors === defaultColor) {
        this.currentShape.color = defaultColor;
      } else {
        this.currentShape.color = this.colors.hex;
      }

      // RETURN TO DEFAULT VIEW
      this.view = 'default';
    }
  },
  components: {
    'chrome-picker': Chrome
  }
}
